import { css } from 'glamor'

const style = {
  timeline: css(
    {
      background: '#091B2B',
      boxShadow: '-8px 25px 50px rgba(0,0,0,0.7)',
      borderTop: '0.8rem solid #72D6D6',
      minHeight: '100vh',
      padding: '4rem 3.5rem',
      width: '100%',
    },
    {
      '@media(min-width: 1024px)': {
        padding: '8rem 7rem',
      },
    }
  ),
  titleWrap: css({
    display: 'flex',
    flexDisplay: 'row',
    position: 'relative',
    paddingBottom: '10rem',
    verticalAlign: 'middle',
  }),
  title: css({
    color: '#F4F5F6',
    display: 'block',
    fontFamily: 'Georgia, serif',
    fontSize: '2.4rem',
    fontWeight: 'bold',
    letterSpacing: '-0.02em',
    margin: 0,
  }),
  date: css({
    color: '#F4F5F6',
    fontSize: '1.3rem',
    fontWeight: '700',
    marginLeft: 'auto',
    position: 'relative',
    top: '-4px',
  }),
  row: css({
    display: 'flex',
    flexDisplay: 'row',
    flexFlow: 'wrap',
    position: 'relatve',
    width: '100%',
  }),
  leftCol: css({
    borderRight: '2px solid #72D6D6',
    minHeight: '10rem',
    position: 'relative',
    width: '50%',
  }),
  rightCol: css({
    borderLeft: '2px solid #72D6D6',
    marginLeft: 'auto',
    minHeight: '10rem',
    position: 'relative',
    width: '50%',
  }),
  dateStamp: css({
    border: '4px solid #72D6D6',
    borderRadius: '4rem',
    color: '#F4F5F6',
    fontSize: '1.6rem',
    fontWeight: '500',
    height: '8rem',
    lineHeight: '7.2rem',
    textAlign: 'center',
    width: '8rem',
  }),
  dateStampLeft: css({
    margin: 0,
    position: 'absolute',
    right: '3.9rem',
    top: '-3.8rem',
  }),
  dateStampRight: css({
    margin: '0',
    position: 'absolute',
    left: '3.9rem',
    top: '-3.8rem',
  }),
  borderThickTop: css({
    borderTop: '4px solid #72D6D6',
  }),
  notchThick: css({
    background: '#72D6D6',
    height: '0.4rem',
    display: 'block',
    position: 'absolute',
    right: 0,
    top: 0,
    width: '4rem',
  }),
  notchThickRight: css({
    background: '#72D6D6',
    height: '0.4rem',
    display: 'block',
    position: 'absolute',
    left: 0,
    top: 0,
    width: '4rem',
  }),
  sourceDivide: css({
    borderBottom: '1px solid #72D6D6',
    color: '#72D6D6',
    display: 'block',
    fontWeight: '400',
    margin: 0,
    height: '4.1rem',
    letterSpacing: '0.05em',
    lineHeight: '3.8rem',
    width: '100%',
  }),
  entry: css({}),
  entryLeft: css({
    borderLeft: '2px solid #72D6D6',
    boxShadow: '-2px 0 0 #72D6D6',
    color: '#F4F5F6',
    display: 'block',
    marginLeft: 'auto',
    paddingLeft: '4rem',
    position: 'relative',
    width: '50%',
    '& h6': {
      margin: 0,
    },
    ':after': {
      background: '#72D6D6',
      bottom: '0',
      content: '""',
      display: 'block',
      height: '0.1rem',
      left: 0,
      position: 'absolute',
      top: '1.7rem',
      width: '2rem',
    },
  }),
  entryRight: css({
    borderRight: '2px solid #72D6D6',
    boxShadow: '2px 0 0 #72D6D6',
    color: '#F4F5F6',
    display: 'block',
    paddingRight: '4rem',
    position: 'relative',
    textAlign: 'right',
    width: '50%',
    '& h6': {
      margin: 0,
    },
    ':after': {
      background: '#72D6D6',
      bottom: '0',
      content: '""',
      display: 'block',
      height: '0.1rem',
      position: 'absolute',
      right: 0,
      top: '1.7rem',
      width: '2rem',
    },
  }),
  entrySpacer: css({
    paddingBottom: '6rem',
  }),
  alignRight: css({
    textAlign: 'right',
  }),
}

export default style



