import React, { PropTypes } from 'react'
import Link from 'gatsby-link'
import animation from '../style/animation'
import archiveTimeline from '../style/timeline'
import background from '../style/background'
import classNames from 'classnames'
import text from '../style/type'
import { css } from 'glamor'

const {
  borderThickTop,
  date,
  dateStamp,
  dateStampLeft,
  entrySpacer,
  notchThick,
  timeline,
  title,
  titleWrap,
  leftCol,
  rightCol,
  row,
  sourceDivide,
  entry,
  entryLeft,
  entryRight,
} = archiveTimeline

const TimelineEntry = ({ flip, project, spacer, task, url }) => {
  const link = url ? (
    <a href={url} target="_blank">
      View
    </a>
  ) : (
    <span>NA</span>
  )
  return (
    <div
      className={classNames({
        [entryRight]: flip,
        [entrySpacer]: spacer,
        [entryLeft]: !flip,
      })}
      {...css(entry, row)}
    >
      <h6 {...text.subTitle}>{project}</h6>
      <p {...text.small}>
        {task} | {link}
      </p>
    </div>
  )
}

TimelineEntry.PropTypes = {
  flip: PropTypes.bool,
  project: PropTypes.string,
  spacer: PropTypes.bool,
  task: PropTypes.string,
  url: PropTypes.string,
}

export default TimelineEntry



