import React, { Component } from 'react'
import Footer from '../components/Footer'
import Header from '../components/Header'
import Link from 'gatsby-link'
import Timeline from '../components/Timeline'
import { css } from 'glamor'
import background from '../style/background'
import grid from '../style/grid'
import layout from '../style/layout'
import text from '../style/type'
import img1 from '../img/work-iysl-home.png'

const { alignRight, col, col1, col2, offset4, row, vertHt9 } = grid
const { ht100, leftCol, rightColAuto, img } = layout
const { link, overview } = text
const { centered } = background

class AboutPage extends Component {
  constructor(props) {
    super(props)
  }

  render() {
    return (
      <div {...ht100}>
        <section {...leftCol}>
          <div {...centered}>
            <Timeline />
          </div>
        </section>
        <main {...rightColAuto}>
          <Header />
          <section>
            <div {...row}>
              <div {...css(col, col2, offset4, vertHt9)}>
                <p {...text.number} {...text.numberOverview}>
                  OO
                </p>
                <h2 {...text.subTitle}>Jonathan Cooper</h2>
                <p {...text.overview}>
                  Hello and welcome to my digital portfolio. Im Jon, a UI
                  Engineering based in Austin, TX. I am pasionate about solving
                  problems and delivering highly usable, performant products.
                  Thank you for checking out my work!
                </p>
              </div>
            </div>
            <div {...row}>
              <div {...css(alignRight, col, col2, offset4)}>
                <h1 {...text.workTitle}>ME</h1>
              </div>
            </div>
          </section>
        </main>
      </div>
    )
  }
}

export default AboutPage



